.card {
    flex: 1 1 0.33%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid $color-3;
    border-radius: 4px;
    margin: 20px;
    max-height: 600px;
    padding: 20px;
    transition: .2s ease;

    &:hover {
        transform: scale(1.05);
        border-color: $color-4;
        background-color: rgba(0, 0, 0, 0.031);
        cursor: pointer;
    }
    
    .card-header {
        text-align: center;
        margin:0 0 10px 0px;
    }

    .card-thumb {
        width: 300px;
        height: 300px;
        margin: 10px;
        img {
            width: 100%;
            object-fit: contain;
            height: 100%;
        }
    }

    .card-description {
        text-align: center;
        overflow-y: auto;
    }
}