header {
    max-width: 100vw;
    height: 80px;
    border-bottom: 1px solid $color-1;
    background-color: $color-2;
    margin-bottom: 50px;
    .container {
        display: flex;
        height: 100%;
        align-items: center;
        margin: 0 20px;
        .title {
            font-size: 1.4rem;
            flex: 1;
            font-weight: 600;
        }

        .search {
            input {
                outline: none;
                border: none;
                padding: 10px;
                border-radius: 5px;
                width: 50%;
            }
            flex: 1;
            display: flex;
            justify-content: flex-end;
        }
    }
}